
import {mapState,mapMutations} from "vuex";
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'

export default {
name:"proitemForm",
data () {
return {
proitemId:this.$route.query.proitemId
}
},
components: {
Group,
XHeader,
XInput,
XButton,
XTextarea,
Selector
},
computed:{
...mapState({
formModel:state => state.common.proitem.formModel,
vm:state => state.common.proitem.vm
})
},
created () {
if(this.proitemId){
this.getProitem(this.proitemId);
}
},
methods: {
...mapMutations({
getProitem:"common/proitem/getProitem"
}),
handleSave(){
if(this.proitemId){
this.$store.commit({
type:'common/proitem/edit'
})
}else{
this.$store.commit({
type:'common/proitem/add'
})
}
}
}
}



